	// import iPrice from '@/components/i-price/i-price.vue'
	// 弹出框样式外联
	import uniPopup from '@/components/uni-popup/uni-popup.vue'
	// 数字角标样式外联
	// import uniBadge from '@/components/uni-badge/uni-badge.vue'
	// 数字输入框样式外联
	// import uniNumberBox from "@/components/uni-number-box/uni-number-box.vue"
	export default{
		components: {
			// iPrice,
			uniPopup
			// uniBadge
			// uniNumberBox
		},
		data() {
			return {
				indicatorDots: true, //是否显示面板指示点
				autoplay: false,  //是否自动切换
				circular:true,  //是否衔接切换
				duration: 500  ,//滑动动画时长
				address: false, //选择地址弹出框
				technology: false, //选择工艺弹出框
				inverted:true, //点击标签的变化
				specs: false,  //选择规格弹出框
				ownunit: true, //自定义宽高
				shop: '',  //购物车数量
				productList: '',  //商品列表
				Price: '',  //商品单价价格
				totalPrice: '',  //商品总价金额
				shopImg: '',  //顶部商品图片
				stuffName: '',  //页面材料名称
				normsInfo: '',  //页面规格：宽高
				numInfo: '',  //页面数量多少
				stuffList: '',  //材料列表
				stuffSelect: [],  //材料选中后的数组
				unitlist: '',  //规格大小列表
				checkNum: -1,  //材料选中下标
				caiLiaoId: '',  //材料id
				ggcheckNum: -1,  //规格大小选中下标
				checkCraft: '',  //工艺选中的状态
				number: 1,  //数量
				craft: [],  //储存工艺数组
				rSelect:[], //全部工艺选中  
				gongYiId: [],  //工艺id
				gongYiName: [], //工艺名称
				cailList: '',  //材料
				gongyList: '',  //工艺
				guigList: '',  //规格
				guigWidth: '',  //规格：宽
				guigHeight: '',  //规格：高
				guigMonad: '',  //规格单位
			}
		},
		methods:{
			// 选择工艺弹出框
			technologyTap(){
				this.technology = true
			},
			// 选择规格弹出框
			specsTap(){
				this.specs = true;
			},
			// 工艺选中样式操作
			craftTap(index,evt){
				this.checkCraft = index;
				if(this.rSelect.indexOf( evt )!= -1){  
                    this.rSelect.splice( this.rSelect.indexOf( evt), 1 );  
					this.gongYiId.splice(this.rSelect.indexOf(evt.th_id) , 1);  //工艺id 存在的话就不添加
					this.gongYiName.splice(this.rSelect.indexOf(evt.th_name) , 1);  //工艺名称 存在的话就不添加
				}else{  
					this.rSelect.push(evt);  
					this.gongYiId.push('~'+evt.th_id+'~'); //获取工艺ID 添加到yongYiId
					this.gongYiName.push(evt.th_name);  //获取工艺名称 添加到gongYiName
                } 
			},
			// 选择工艺的确定按钮
			confirmCraft(e){
				this.technology = false;
				this.offer();
			},
			// 点击自定义规格
			custom(e){
				this.ggcheckNum = -2;
				this.ownunit = false;
			},
			// 自定义规格:宽
			widthInput(e){
				if(e.detail.value == 0){
					uni.showToast({
						title:'宽不能为0',
						icon:'none'
					})
				}else{
					this.guigWidth = e.detail.value;
				}
			},
			// 自定义规格:高
			heightInput(e){
				if(e.detail.value == 0){
					uni.showToast({
						title:'宽不能为0',
						icon:'none'
					})
				}else{
					this.guigHeight = e.detail.value;
				}
			},
			// 选择材料
			makings(index,evt){
				this.cailList = evt.g_name;  //将选中的材料 打印到上面显示
				this.caiLiaoId = evt.g_id; //获取材料id
				this.checkNum = index;
				uni.request({
					url: 'http://192.168.199.194/mobile/order/shiftsTechelog',
					method: 'GET',
					data:{
						gid: evt.g_id
					},
					success: (res) => {
						if(res.data.status == 0){
							this.craft = res.data.technology;  //给craft（工艺数组）赋值
						}
					}
				})
			},
			// 选择规格
			norms(e,ggList){
				this.ownunit = true
				this.guigWidth = ggList.width;
				this.guigHeight = ggList.height;
				this.guigMonad = ggList.monad;
				this.ggcheckNum = e;
			},
			// 数量 ： 减
			proReduceBtn(e){
				let i = 1;
				if(e > 1){
					let reduceNum = e - i;
					this.number = reduceNum;
				}
			},
			// 数量 ： 加
			proPlusBtn(num){
				let i = 1;
				let NumBer =num+ i;
				this.number= NumBer;
			},
			//数量 ： 输入框
			peoNumName(e){
				this.number = e.target.value;
			},
			// 报价函数
			offer(){
				let jsonList = uni.getStorageSync("jsonList");  //获取存储在Storage里的值
				let data = JSON.parse(jsonList); //JSON字符串转对象
				let dbname = data.dbname; //数据库
				let token = data.softdog; //加密狗
				let org = data.org; //访问IP地址
				let Partnerid = data.csid;  //客户ID
				this.caiLiaoId;  //材料ID
				this.guigWidth;  //规格：宽
				this.guigHeight;  //规格：高
				this.number;  //数量
				this.gongYiId;  //工艺ID
				if(this.checkNum == -1){
					uni.showToast({
						title: '必须有一个材料存在',
						icon: 'none'
					});
					return 1;
				}else if(this.guigWidth == '' || this.guigHeight == ''){
					uni.showToast({
						title: '规格必须有一个存在',
						icon: 'none'
					});
					return 1;
				}else {
					
					let urlInfo = '{"params": [{"goodsid": "'+this.caiLiaoId+'","partnerid": "'+Partnerid+'","w": "'+this.guigWidth+'","h": "'+this.guigHeight+'","LpidList": "'+this.gongYiId+'","qty": "'+this.number+'"}]}';
					uni.request({
						url: "http://"+org+"/services/order/query",
						method: 'GET',
						data:{
							token:token, //令牌
							dbname:dbname,  //数据库
							data:urlInfo,  //其他参数
						},
						success: (res) => {
							// console.log(res)
							if(res.data.status == 0){
								let price = res.data.QueryResults[0]['price'];  //单价
								let disPirce = res.data.QueryResults[0]['discprice'];  //折扣后单价 （成交单价）
								let discMoney = res.data.QueryResults[0]['disctotalmoney'];  //当前行的小计（以折后单价计算）+ 后期工艺价格
								let totalMoney = res.data.QueryResults[0]['totalmoney'];  //当前行的小计（以指导单价计算）+ 后期工艺价格
								// 判断: 折扣后单价是否为空
								if (typeof(res.data.QueryResults[0]['discprice']) === 0){
									this.totalPrice = totalMoney.toFixed(2);
									this.Price = price.toFixed(2);
								}else {
									this.totalPrice = discMoney.toFixed(2);
									this.Price = disPirce.toFixed(2);
								}
							}else if(res.data.status == 4){
								uni.showToast({
									title: '请录入宽高及其选择材料后再继续！',
									icon: 'none'
								})
							}else{
								uni.showToast({
									title: '未获取到加密狗信息！',
									icon: 'none'
								})
							}
						},
						fail: (res) => {
							uni.showToast({
								title: '系统繁忙，请稍后再试！',
								icon: 'none'
							})
						}
					})      
				}
			},
			// 规格选项中的取消按钮
			specBtnsCart(e){
				this.specs = false
			},
			// 规格选项中的确定按钮
			specBtnsPayment(e){
				this.specs = false;
				this.stuffName = this.cailList; //给主页面的材料赋值
				this.normsInfo = this.guigWidth +'*'+ this.guigHeight + this.guigMonad; //给主页面的规格赋值
				this.numInfo = this.number;  //给主页面的数量赋值
				this.offer();  //调用报价接口
			},
			// 加入购物车按钮
			addToCart(e){
				let ruslt = this.offer();  //调用报价接口
				if (ruslt == 1){
					return false;
				}
				let jsonList = uni.getStorageSync("jsonList");  //获取存储在Storage里的值
				let data = JSON.parse(jsonList); //JSON字符串转对象
				let Signboard = data.Ident_Signboard; //令牌
				let Signguid = data.Ident_Signguid; //用户id
				this.guigWidth;  //规格：宽
				this.guigHeight;  //规格：高
				this.caiLiaoId;  //材料ID
				this.number;  //数量
				this.Price; //单价
				this.totalPrice;  //总价
				this.gongYiId;  //工艺ID
				this.gongYiName; //工艺名称
				
				uni.request({
					url: 'http://192.168.199.194/mobile/Classification/joinShop',
					method: 'GET',
					data: {
						Ident_Signboard: Signboard,  //令牌
						Ident_Signguid: Signguid,  //用户id
						long:this.guigHeight, //高
						kuan:this.guigWidth,  //宽
						clid:this.caiLiaoId,  //材料id
						qty:this.number,  //数量
						total:this.totalPrice,  //总价
						price:this.Price,  //单价
						gyid:this.gongYiId,  //工艺id 
						gyname:this.gongYiName,  //工艺名称
					},
					success: (res) => {
						if(res.data.status == 0){
							this.shop = (this.shop) + 1;  //购物车数量+1
							data.shop = this.shop;  //data.shop 重新赋值
							let json = JSON.stringify(data);  //对象转字符串
							uni.setStorageSync('jsonList',json) //将shop数据库数量从新存储进去
							uni.showToast({
								title: '加入购物车成功！',
								icon: 'success',
							})
						}else if(res.data.status == 202 || resd.data.status == 201){
							uni.showToast({
								title: '登录已过期，请重新登录！',
								icon: 'none'
							})
						}else{
							uni.showToast({
								title: '加入购物车失败！',
								icon: 'none'
							})
						}
					},
					fail: (res) => {
						uni.showToast({
							title: '系统繁忙，请稍后再试!',
							icon: 'none'
						})
					}
				})
			},
			
			// 直接购买按钮
			immediatePayment(e){
				let ruslt = this.offer();  //调用报价接口
				if (ruslt == 1){
					return false;
				}
				let jsonList = uni.getStorageSync("jsonList");  //获取存储在Storage里的值
				let data = JSON.parse(jsonList); //JSON字符串转对象
				let Signboard = data.Ident_Signboard; //令牌
				let Signguid = data.Ident_Signguid; //用户id
				uni.navigateTo({
					url: '../order/payment?Ident_Signboard='+ Signboard+'&Ident_Signguid='+Signguid+'&long='+this.guigHeight+'&kuan='+this.guigWidth
					+'&clid='+this.caiLiaoId+'&qty='+this.number+'&total='+this.totalPrice+'&price='+this.Price+'&gyid='+this.gongYiId+'&gyname='+this.gongYiName
					+'&spid='+this.productList.ty_id+'&l=2'	
				})
			},
			// 购物车图标按钮 跳转
			shoppingCart(e){
				uni.switchTab({
					url: '../shopping/shopping'
				})
			}
		},
		
		// 刚进入页面加载数据
		onLoad:function(options){
			let jsonList = uni.getStorageSync("jsonList");  //获取存储在Storage里的值
			let data = JSON.parse(jsonList); //JSON字符串转对象
			this.shop = data.shop; //给shop(购物车数量)赋值
			console.log(options.type)
			uni.request({
				url: 'http://192.168.199.194/mobile/order/productInfo',
				method: 'GET',
				data:{
					gid:options.type
				},	
				success: (res) => {
					if(res.data.status == 0){
						// console.log(res.data.goods.ty_id)
						this.productList = res.data.goods;  //给productList(商品列表)赋值
						this.shopImg = res.data.shopimg;  //给shopImg(顶部商品图片)赋值
						this.stuffList = res.data.stuff;  //给stuffList(材料列表)赋值
						this.unitlist = res.data.unit;  //给unitlist(规格列表)赋值
						this.Price = res.data.goods.price;  //给Price（商品价格）赋值
					}
				}
			})
		}
	}
